<template lang='pug'>
    div.main-content(v-loading='loading')
        el-card.order-left
            .search-block
                span 用户选择
                |&nbsp;&nbsp;&nbsp;&nbsp;
                el-select(v-model='userNo', filterable, style='width: 200px', @change='selectUser', placeholder='手机号码选择乘机用户')
                    el-option(v-for='(item, index) in userList', :value='index', :key='item.Phone', :label='item.Phone + " - " + item.Name')
                |&nbsp;
                el-button(type='warning', round, icon='el-icon-plus') 添加用户
            .friend-block
                h3.friend-block__head 机主关联乘客
                    span.friend-block__head_sub (跟机主关联的所有乘客)
                el-card.friend-block__card
                    p.friend-block__card_host(v-if='userNo!==null') {{userList[userNo].Name}}(机主)
                    el-checkbox-group(v-model='friendNo')
                        el-checkbox(v-for='(item, index) in friends', :label='index', :key='item.Name') {{item.Name}}
                el-button(type='info', @click='addModel=true', icon='el-icon-plus') 新增乘客
                el-dialog(title='新增机主', :visible.sync='addModel', width='30%')
                //- Modal(v-model='addModel', title='新增机主', @on-ok='addOk', @on-cancel='addCancel')
                //-     p kkkkkkkkkkkkkkkkkk
            .passenger-block
                h3.passenger-block__head 乘机人信息
                .passenger-block__card(v-for='item in friendNo')
                    .passenger-block__card_left
                        p: i.el-icon-edit
                    .passenger-block__card_middle
                        p.line
                            span.label 姓名:
                            span.detail {{friends[item].Name}}
                        p.line
                            span.label 身份证:
                            span.detail {{friends[item].thisID}}
                        p.line
                            span.label 电话:
                            span.detail {{friends[item].Phone}}
                    .passenger-block__card_right
                        p.line
                            //- span.type 成人
                            span.detail(v-if='friends[item].age>=12') 成人：￥{{cardObj.price.fdPrice}}
                            span.detail(v-else-if='friends[item].age>=2') 儿童：￥{{cardObj.price.fdPrice*0.5}}
                            span.detail(v-else) 婴儿：￥{{cardObj.price.fdPrice*0.1}}
            .select-block
                .select-block__choice
                    p.select-block__choice_line
                        el-checkbox(v-model='insurance', size='large') 航空意外险
                .select-block__choice
                    p.select-block__choice_line
                        el-checkbox(v-model='invoice', size='large') 报销凭证(到付)
        el-card.order-right
            el-card.ticket-card
                .ticket-card__head
                    h3.ticket-card__head_title
                        span {{cardObj.departDate}}
                        span {{cardObj.departCityName}} - {{cardObj.arriveCityName}}
                    p.ticket-card__head_sub
                        span {{cardObj.airline}}
                        span {{cardObj.no}}
                        span(v-if='cardObj.level==="Y"') 经济{{cardObj.cabin}}舱
                        span(v-else-if='cardObj.level==="F"') 头等{{cardObj.cabin}}舱
                        span(v-else-if='cardObj.level==="C"') 商务{{cardObj.cabin}}舱
                .ticket-card__journey
                    .ticket-card__journey_left
                        h3.clock {{cardObj.departTime}}
                        p.airport {{cardObj.departPort}}
                    .ticket-card__journey_middle
                        p.last {{cardObj.flightTime}}
                        p.line
                    .ticket-card__journey_right
                        h3.clock {{cardObj.arriveTime}}
                        p.airport {{cardObj.arrivePort}}
                hr
                .ticket-card__content(v-if='adtPrice.num || chdPrice.num || babyPrice.num')
                    .ticket-card__content_line(v-if='adtPrice.num')
                        p.line
                            span.line_label 成人票
                            span.line_price ￥{{cardObj.price.fdPrice}} X {{adtPrice.num}}
                    .ticket-card__content_line(v-if='chdPrice.num')
                        p.line
                            span.line_label 儿童票
                            span.line_price ￥{{this.cardObj.price.fdPrice}} X {{chdPrice.num}} X 0.5
                    .ticket-card__content_line(v-if='babyPrice.num')
                        p.line
                            span.line_label 婴儿票
                            span.line_price ￥{{this.cardObj.price.fdPrice}} X {{babyPrice.num}} X 0.1
                    .ticket-card__content_line(v-if='adtPrice.num || chdPrice.num || babyPrice.num')
                        p.line
                            span.line_label 机建费
                            span.line_price
                                span(v-if='!adtPrice.num && !chdPrice.num') ￥0
                                span(v-else)
                                    span.line_price_adt(v-if='adtPrice.num') ￥{{cardObj.price.taxFee}} X {{adtPrice.num}}
                                    span.line_price_add(v-if='adtPrice.num && chdPrice.num') +
                                    span.line_price_chd(v-if='chdPrice.num') {{cardObj.price.taxFee}} X {{chdPrice.num}} X 0.5
                    .ticket-card__content_line(v-if='adtPrice.num || chdPrice.num || babyPrice.num')
                        p.line
                            span.line_label 燃油费
                            span.line_price ￥{{cardObj.price.fuelFee * friendNo.length}}
                    hr
                    .ticket-card__content_line(v-if='insurance')
                        p.line
                            span.line_label 航空意外险
                            span.line_price ￥5 X {{friendNo.length}}
                    .ticket-card__content_line(v-if='invoice')
                        p.line
                            span.line_label 报销凭证
                            span.line_price 到付
                    .ticket-card__content_line
                        p.total-price
                            |￥{{totalPrice}}
                .ticket-card__confirm(v-if='adtPrice.num || chdPrice.num || babyPrice.num')
                    el-button(type='primary', style='width: 100%', @click='confirmSend') 发给用户确认并自动预定

</template>

<script>
export default {
    data () {
        return {
            userList: [],
            userNo: null,
            friends: [],
            friendNo: [],
            insurance: false,
            invoice: false,
            cardObj: {},
            orderObj: {},
            addModel: false,
            loading: false
        }
    },
    computed: {
        adtPrice () {
            let price = 0
            let num = 0
            for (let i in this.friendNo) {
                if (this.friends[i].age >= 12) {
                    price += this.cardObj.price.fdPrice
                    num++
                }
            }
            return {price, num}
        },
        chdPrice () {
            let price = 0
            let num = 0
            for (let i in this.friendNo) {
                if (this.friends[i].age < 12 && this.friends[i].age >= 2) {
                    price += this.cardObj.price.fdPrice * 0.5
                    num++
                }
            }
            return {price, num}
        },
        babyPrice () {
            let price = 0
            let num = 0
            for (let i in this.friendNo) {
                if (this.friends[i].age < 2) {
                    price += this.cardObj.price.fdPrice * 0.1
                    num++
                }
            }
            return {price, num}
        },
        totalPrice () {
            let price = this.adtPrice.price + this.chdPrice.price + this.babyPrice.price + this.cardObj.price.taxFee * this.adtPrice.num + this.cardObj.price.taxFee * this.chdPrice.num * 0.5 + this.cardObj.price.fuelFee * this.friendNo.length
            if (this.insurance) {
                price += 5 * this.friendNo.length
            }
            return price
        }
    },
    methods: {
        selectUser () {
            this.friendNo = []
            this.friends = []
            let _this = this
            // this.$http.get('/friend/searchbybuyerphone/', {
            this.$http.get('/friend/searchbybuyerphone/', {
                params: {
                    phone: _this.userList[_this.userNo].Phone
                }
            }).then(function (res) {
                let host = [
                    {
                        Name: _this.userList[_this.userNo].Name,
                        Phone: _this.userList[_this.userNo].Phone,
                        thisID: _this.userList[_this.userNo].thisID
                    }
                ]
                _this.friends = host.concat(res.data)
                for (let item in _this.friends) {
                    _this.friends[item].age = _this.getAge(_this.friends[item].thisID)
                }
            }).catch(function (error) {
                console.log(error)
            })
        },
        getAge (cardID) {
            let nowDate = new Date(this.cardObj.departDate)
            let month = nowDate.getMonth() + 1
            let day = nowDate.getDate()
            let age = nowDate.getFullYear() - cardID.substring(6, 10) - 1
            if (cardID.substring(10, 12) < month || (cardID.substring(10, 12) === month && cardID.substring(12, 14) <= day)) {
                age++
            }
            return age
        },
        addOk () {
            this.$Message.info('ok')
        },
        addCancel () {
            this.$Message.info('cancel')
        },
        confirmSend () {
            let _this = this
            this.loading = true
            this.orderObj.cName = this.userList[this.userNo].Name
            this.orderObj.cPhone = this.userList[this.userNo].Phone
            this.orderObj.prices = []
            this.orderObj.travelers = []
            for (let i in this.friendNo) {
                let priceObj = this.cardObj.price
                let travelerObj = {
                    'ageType': '',
                    'name': '',
                    'cardType': '',
                    'cardNo': '',
                    'phone': ''
                }
                if (this.friends[i].age >= 12) {
                    priceObj.ageType = 'ADT'
                    travelerObj.ageType = 'ADT'
                } else if (this.friends[i].age >= 2) {
                    priceObj.ageType = 'CHD'
                    travelerObj.ageType = 'CHD'
                } else {
                    priceObj.ageType = 'INF'
                    travelerObj.ageType = 'INF'
                }
                travelerObj.name = this.friends[i].Name
                travelerObj.cardType = 'NI'
                travelerObj.cardNo = this.friends[i].thisID
                travelerObj.phone = this.friends[i].Phone
                this.orderObj.prices.push(priceObj)
                this.orderObj.travelers.push(travelerObj)
            }
            this.$http.post('/jp/buyPlaneTicket/', _this.orderObj).then(function (res) {
                console.log(res.data)
                if (res.data.code === '200') {
                    _this.$router.push('/flightContact')
                } else {
                    _this.loading = false
                    _this.$message('订单提交失败，请重新发送订单')
                }
            }).catch(function (error) {
                console.log(error)
            })
            console.log(this.orderObj)
        }
    },
    created () {
        let queryObj = this.$route.query
        let _this = this
        console.log(queryObj)
        this.cardObj = queryObj.cardObj
        this.orderObj = queryObj.orderObj
        this.$http.get('/user/showall/')
            .then(function (res) {
                _this.userList = res.data
            }).catch(function (error) {
                console.log(error)
            })
    }
}
</script>

<style scoped>
    .main-content {
        display: flex;
        padding: 10px;
    }
    .order-left, .order-right {
        flex-grow: 1;
        margin: 0 5px;
    }
    .search-block {
        border-bottom: 1px solid #Dfdfdf;
        padding-bottom: 10px;
        margin-bottom: 10px;
    }
    .friend-block__head, .passenger-block__head {
        font-weight: bold;
    }
    .friend-block__head_sub {
        font-weight: normal;
        font-size: 15px;
    }
    .friend-block__card {
        border-radius: 0;
        background-color: #f5f5f5;
        margin: 5px 0;
    }
    .friend-block__card_host {
        margin-bottom: 5px;
        font-size: 18px;
    }
    .passenger-block {
        margin-top: 20px;
    }
    .passenger-block__card {
        border-radius: 0;
        background-color: rgb(250, 232, 232);
        padding: 15px;
        margin: 5px;
        display: flex;
        align-items: center;
        /* justify-content: space-between; */
    }
    .passenger-block__card:hover {
        box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
        position: relative;
    }
    .passenger-block__card_left {
        flex-grow: 1;
    }
    .passenger-block__card_middle {
        flex-grow: 6;
    }
    .passenger-block__card_right {
        flex-grow: 3;
    }
    .passenger-block__card_middle > .line {
        margin: 10px 0;
    }
    .passenger-block__card_middle > .line > .label {
        display: inline-block;
        width: 100px;
    }
    .passenger-block__card_middle > .line > .detail {
        margin-left: 5px;
        display: inline-block;
        width: 200px;
        border-bottom: 1px solid black;
    }
    .passenger-block__card_right > .line > .type {
        margin-right: 10px;
    }
    .select-block__choice {
        margin: 10px;
    }
    .ticket-card {
        width: 450px;
        background-color: #f4f8f9;
        border: 1px solid #d7dce0;
        border-radius: 0;
        text-align: center;
        padding-top: 10px;
    }
    .ticket-card__head_title > span {
        margin: 10px;
        font-size: 24px;
        font-weight: bold;
    }
    .ticket-card__head_sub {
        margin: 5px 0;
        font-size: 16px;
    }
    .ticket-card__head_sub > span {
        color: #88949a;
        margin: 0 3px;
    }
    .ticket-card__journey {
        padding: 10px;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    .ticket-card__journey_left > .clock, .ticket-card__journey_right > .clock {
        font-size: 30px;
    }
    .ticket-card__journey_left > .airport, .ticket-card__journey_right > .airport {
        font-size: 12px;
    }
    .ticket-card__journey_middle {
        padding: 0 20px;
        margin-top: -10px;
        flex-grow: 1;
    }
    .ticket-card__journey_middle > .line {
        border-top: 1px solid black;
        width: 100%;
    }
    .ticket-card__journey_middle > .line::after {
        content: '';
        display: block;
        float: right;
        width: 10px;
        border-top: inherit;
        transform: rotate(30deg);
        margin-top: -3px
    }
    .ticket-card__content {
        background: white;
        margin: 20px;
        padding: 10px;
    }
    .ticket-card__content_line {
        margin: 10px;
        height: 30px;
        line-height: 30px;
    }
    .ticket-card__content_line > .line {
        font-size: 18px;
        display: flex;
        justify-content: space-between;
    }
    .ticket-card__content_line > p.total-price {
        text-align: right;
        font-size: 30px;
        color: red;
    }
</style>



